.tokenButtonsContainer{
  background-color: unset!important;
  box-shadow: unset!important;
}
.tokenButtons{
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  padding-right: 0;
  padding-left: 0;
  padding-top: 1rem;
  padding-bottom: 0;
}
.tokenButtons:last-child{
  padding-bottom: 0;
}
.tokenButtons a:last-child{
  margin-right: 0;
}
a.shinyButtonUI{
  margin-right: .5rem;
  padding: 1.3rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  line-height: 1;
  border-radius: .3rem;
}
a.purpleButton {
  background-color: #8247e5;
  color: #f7f3ef;
}
a.purpleButton:hover {
  background-color: #D92F85;
  color: #f7f3ef;
}
a.fuxiaButton {
  background-color: #e45999;
  color: #FFD148;
}
a.violetButton {
  background-color: #7014f9;
  color: #FFD148;
}
a.yellowButton{
  background-color: #FFD148;
  color: #000000;
}
.yellowButton:last-child {
    margin-right: 0;
}
a.lightPurpleButton {
  background-color: #E766FF;
  color: #FFD148;
}
a.lightGreenButton {
  background-color: #A7E7E0;
  color: #8247e5;
}
.yellowButton:last-child, .lightGreenButton:last-child, .lightPurpleButton:last-child {
    margin-right: 0;
}
.ballerzBox{
  display: block;
  margin-top: 1rem;
  margin-bottom: 1rem;
}
.ballerzImg{
  display: block;
  position: relative;
  width: 302px;
  max-width: 100%;
  z-index: 0;
  left: 0;
  right: 0;
  margin: auto;
  margin-bottom: .5rem;
}
.ballerzTextBox{
  font-size: 1.2em;
  align-self: center;
  flex-direction: row;
  padding: 2rem;
  justify-content: space-between;
  background-color: #371255;
  border-radius: 3rem;
}
.ballerzText h2{
  font-size: 2.25em;
  margin-bottom: 1rem;
}
.ballerzText p{
  line-height: 1.2em;
  margin: 0;
  margin-bottom: .5rem;
}
.ballerzText p a{
  color: white;
  margin-left: .1em;
}
.ballerzText .shinyButton {
  margin-top: .5rem;
  margin-right: .5rem;
  font-size: 1em;
  line-height: 1;
  padding: .5rem;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  border-radius: .2rem;
  background-color: #371255;
  color: white;
  border:1px inset white;
  text-transform: unset;
}
.shinyButton.bGradient{
  background: #be05a3;
  background: -moz-linear-gradient(left,  #be05a3 0%, #9521d4 100%);
  background: -webkit-linear-gradient(left,  #be05a3 0%,#9521d4 100%);
  background: linear-gradient(to right,  #be05a3 0%,#9521d4 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#be05a3', endColorstr='#9521d4',GradientType=1 );
  border: unset;
  margin-bottom: .5rem;
}
.shinyButton.bGradient:hover{
  background: #be05a3;
  background: -moz-linear-gradient(left,  #be05a3 50%, #9521d4 100%);
  background: -webkit-linear-gradient(left,  #be05a3 50%,#9521d4 100%);
  background: linear-gradient(to right,  #be05a3 50%,#9521d4 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#be05a3', endColorstr='#9521d4',GradientType=1 );
}
p.yellowText{
  color: #FFB607;
}
.textBigger{
  font-size: 1.2em;
}
.tvlAll{
  display: flex;
  position: relative;
  border-radius: 2rem!important;
  background: #be05a3;
  line-height: 1.2em;
  min-height: 15vh;
  background: -moz-linear-gradient(left,  #DF3A74 50%, #F78D5A 100%);
  background: -webkit-linear-gradient(left,  #DF3A74 50%,#F78D5A 100%);
  background: linear-gradient(to right,  #DF3A74 50%,#F78D5A 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#DF3A74', endColorstr='#F78D5A',GradientType=1 );
  box-shadow: 0 5px 0 #B42456;
  margin-top: 1rem;
  overflow: hidden;
}
.tvlAll h3{
  position: relative;
}
.tvlAllContent{
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.tvlCount{
  font-size: 2.5em;
  bottom: 0.3rem;
  position: relative;
  line-height: 1;
  padding-top: 1.5rem;
}
.safeImg{
  position: absolute;
  right: 0;
  height: 90%;
  width: auto;
  bottom: 0;
  margin-bottom: .3rem;
}
.kaxyml{
  background-color: unset;
  display: unset;
  height: auto;
  width: auto;
  border-radius: unset;
  align-items: inherit;
  justify-content: unset;
  margin: 0;
  font-size: inherit;
  -webkit-box-pack: inherit;
}
.tokenCard{
  position: relative;
  border-radius: 1rem;
  background-color: #4F179B;
  margin: .5rem;
}
.tokenCard::before{
  content: "";
  position: absolute;
  height: .3em;
  width: 50%;
  left: 0;
  right: 0;
  margin-left: auto;
  margin: auto;
}
.tokenCard.yellowText::before{
  background-color: #FFD148;
}
.tokenCard.lillaText::before{
  background-color: #E766FF;
}
.tokenCard.greenText::before{
  background-color: #A7E7E0;
}
.cardContainer{
  padding: 1rem;
}
.tokenBox{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.infoToken{
  margin-left: 1rem;
}
.infoToken h4{
  font-size: 2em;
  display: inline;
  margin-right: .5rem;
}
.tokenCard.yellowText h4{
  color: #FFD148;
}
.tokenCard.lillaText h4{
  color: #E766FF;
}
.tokenCard.greenText h4{
  color: #A7E7E0;
}
.infoToken span{
  color: #9D94AB;
}
.infoToken a, .infoToken a:hover{
  display: block;
  line-height: 1;
  text-decoration: none;
  color: #A393BC;
  margin-top: .3rem;
}
.infoToken svg{
  vertical-align: middle;
  margin-left: .3rem;
  fill: #A393BC;
  max-width: 15px;
  height: auto;
}
.metaMaskLink{
  position: absolute!important;
  top: 0;
  right: 0;
}
.metaMaskLink::before{
  content: "+";
  font-size: 1.2em;
  font-weight: 700;
}
.currentPriceTitle{
  display: block;
  color: #A393BC;
  border-top: 1px solid #A393BC;
  width: 100%;
  text-align: center;
  margin-top: .3rem;
  margin-bottom: .3rem;
  padding-top: .5rem;
  text-transform: uppercase;
}
.currentPriceBox{
  background-color: #380D74;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  border-radius: .5rem;
  font-size: 1.5em;
  box-shadow: 0 0 8px #A393BC ;
}
.currentPriceBox span{
  padding: 1rem;
  color: #55F89E;
}
.currentPriceBox .yellowText{
  padding: 1rem;
  color: #FFD148;
}
.currentPriceBox .lillaText{
  padding: 1rem;
  color: #E766FF;
}
.currentPriceBox .greenText{
  padding: 1rem;
  color: #A7E7E0;
}
.supplyInfo{
  padding-top:1rem;
  padding-bottom:1rem;
  display: block;
}
.supplyInfo li{
  color: #A393BC;
  line-height: 1.5;
}
.supplyInfo li span{
  color: white;
  position: absolute;
  right: 0;
  margin-right: 1rem;
}
.buyFarmToken{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 0;
}
.buyFarmToken a{
  width: 35%;
}
.buyFarmToken a:first-child{
  width: 65%;
  color: black;
}
.buyFarmToken a:last-child{
  margin-right:0;
}
.tokenCard.pair a.shinyButtonUI span{
  color: #000000;
}
.tokenCard a.MuiListItem-button:hover{
  background-color: #371255;
}
.tokenCard.pair img{
  width: 150px;
  max-width: 100%;
  height: auto;
  margin-bottom: 1rem;
}
.pricePair{
  padding-left: 1rem;
  position: relative;
}
.pair .tokenBox h2{
  display: block;
  margin-bottom: .1rem;
  width: 100%;
}
.pricePair span:nth-child(even){
  margin-right: 1rem;
  margin-bottom: .5rem;
  display: block;
}
.pricePair span:nth-child(3){
  color: #FFD148;
}
.infoPair{
  margin-top: 1rem;
  border-radius: .5rem;
  border: 1px solid #9421D8;
  margin-bottom: .5rem;
}
.infoPairTab{
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #A393BC;
  line-height: 1.5;
  padding: 1rem;
}
.infoPairTab:nth-child(even):before{
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin-left:1rem;
  margin-right:1rem;
  background-color: #9421D8;
  height: 1px;
}

.infoPairTab span{
  color: white;
  font-size: 1.2em;
}
.singldPD .tokenBox{
  justify-content: space-between;
}
.singldPD .tokenBox img{
  vertical-align: middle;
  margin-bottom: .5rem;
}
.singldPD .tokenBox h2{
  margin-left: .5rem;
  display: inline;
  margin-bottom: 1rem;
}
.singleStakingPool{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  border: 1px solid;
  border-radius: .5rem;
}
.purplePool{
  border-color: #9421D8;
}
.purplePool a{
  margin-top: .5rem;
}
.MuiCardContent-root{
  padding: .5rem;
}
.MuiCardContent-root .MuiCardContent-root:last-child{
  padding: 0;
  padding-top: .5rem;
}
 .MuiCardContent-root:last-child{
   padding: 1rem;
 }
 .communityLinks{
   display: flex;
   align-items: flex-start;
   flex-direction: column;
   justify-content: flex-start;
 }
 .twitterIconHome{
   position: relative;
   display: inline-block;
   fill: white;
   margin-top: 1rem;
   width: 24px;
   height: 24px;
   margin-top: 0;
 }
.twitterIconHome svg{
  position: absolute;
  bottom: 0;
  left: 0;
  width: auto;
  height: 100%;
  opacity: 1;
}
.twitterIconHome:hover svg{
  transition: margin .3s ease-in, opacity .5s;
  margin-bottom: 15px;
  margin-left: 8px;
  opacity: 0;
}
@media screen and (min-width:576px) {
  .communityLinks{
    display: flex;
    flex-direction: row;
    align-items: center;
  }
}
 @media screen and (min-width:768px) {
   .ballerzTextBox{
    display: flex;
  }
  .tvlCount{
    font-size: 2.5em;
  }
  .tokenBox{
    flex-direction: row;
  }
  .singldPD .tokenBox img{
    margin-bottom: unset;
  }
  .shinyButton.bGradient{
    margin-bottom: 0;
  }
 }
@media screen and (min-width:992px) {
  .ballerzBox{
    display: flex;
    flex-direction: row;
    margin-top: 0!important;
  }
  .ballerzTextBox{
    border-radius: 0rem;
    border-top-right-radius: 3rem;
    border-bottom-right-radius: 3rem;
    margin-left: -7rem;
    padding-left: 9rem;
    width: 100%;
    background-color: #371255;
  }
  .tvlAll{
    width: 40%;
    margin-right: -3rem;
    margin-bottom: -3rem;
    margin-top: 0;
  }
  .tvlCount{
    font-size: 2.8em;
  }
  .infoPairTab:nth-child(even):before{
    width: 1px;
    top: 0;
    margin-top: 1rem;
    margin-bottom: 1rem;
    right: unset;
    margin-left:unset;
    margin-right:unset;
    height: unset;
  }
  .pricePair span:nth-child(even){
    margin-right: 1rem;
    display: inline;
    margin-bottom: 0;
  }
}
@media screen and (min-width:1200px) {
  .ballerzBox{
    display: flex;
    flex-direction: row;
    margin-top: -2rem!important;
  }
  .tvlCount{
    font-size: 3em;
  }
}